import React, { Component } from 'react';
import { Badge, DropdownItem, DropdownMenu, DropdownToggle, Nav } from 'reactstrap';
import PropTypes from 'prop-types';

import { AppHeaderDropdown, AppNavbarBrand, AppSidebarToggler } from '@coreui/react';
import logo from '../assets/img/brand/trickleUpLogo.png'

const propTypes = {
  children: PropTypes.node,
};

const defaultProps = {};

class LoginHeader extends Component {

  constructor(props){
    super(props);
  }

  render() {

    const { children, ...attributes } = this.props;

    return (
      <React.Fragment >
        <AppNavbarBrand
          full={{ src: logo, width: 89, height: 25, alt: 'Trickle Up Logo' }}
          style= {{backgroundColor : '#ffffff'}}
        />
      </React.Fragment>
    );
  }
}

LoginHeader.propTypes = propTypes;
LoginHeader.defaultProps = defaultProps;

export default LoginHeader;



