import * as actionTypes from "../actions/actionTypes";

const initialState = {
    statesList : [],
    states:[]
};

const stateReducer = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.GET_STATES_LIST:
      return {
        ...state,
        statesList : action.stateList,
        states : action.states
      };
    default:
      return state;
  }
};
export default stateReducer;




