import React, { Component } from "react";
import {
  Button,
  Card,
  FormGroup,
  CardBody,
  CardGroup,
  Col,
  Container,
  Form,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Row, CardDeck
} from "reactstrap";
import { AppHeader } from "@coreui/react";
import FormLayout from "../../components/FormLayout/FormLayout";
import InputElement from "../../components/InputElement/InputElement";
import LoginHeader from "../../components/LoginHeader";
import background from '../../assets/img/backgroundImage.jpg';
import { Link } from "react-router-dom";
import { Redirect } from "react-router-dom";
class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: {
        userName: "",
        password: ""
      },
      userNameRequired: false,
      passwordRequired: false,
      inCorrect:false
    };
  }
  
  onSubmit = () => {
    const { userName, password } = { ...this.state.user };
    if (userName && password) {
      if (userName === "chandan.mishra@gmail.com" && password == 12345) {
        localStorage.setItem("user" ,userName);
       this.props.history.push('/dashboard');
      } else{
          this.setState({inCorrect :true})
      }
    } else {
      this.setState({
        userNameRequired: true,
        passwordRequired: true
      });
    }
  };
  onChangeHandler(event) {
    let user = { ...this.state.user };
    user[event.target.name] = event.target.value;
    this.setState({
      user: user,
      userNameRequired: false,
      passwordRequired: false,
      inCorrect :false
    });
  }
  render() {
    return <div className="app">
        <AppHeader fixed>
          <LoginHeader />
        </AppHeader>
        <div className="app-body" style={{ backgroundColor: "#d3ecec" }}>
          <main className="main">
            <div className="app flex-row" style={{ margin: 40 }}>
              <Container style={{ backgroundColor: "#d3ecec" }}>
                <div>
                  <Row className="justify-content-center">
                    <Col md="6">
                      <CardGroup>
                        {/* <Col xs="12" md="8">
                      <Card className="p-4 card-scroll-box style-4::-webkit-scrollbar-track" >
                        <CardBody  >
                          <h1 className="text-value-lg">About Us</h1>
                          <p className="text-lg card-text-color card-style"><i className="fa fa-align-justify" /> Our Vision</p>
                          <p className="text-muted-sm card-text-color">&nbsp; A world where no one lives in extreme poverty and vulnerability.</p>
                          
                          <p className="text-lg card-text-color card-style"><i className="fa fa-align-justify" /> Our Mission</p>
                          <p className="text-muted-sm card-text-color">&nbsp; Trickle Up helps people in extreme poverty and vulnerability advance their economic and social well-being. We drive large-scale change by partnering with governments, global institutions, and local organizations.</p>
                          
                          <p className="text-lg card-text-color card-style" ><i className="fa fa-align-justify" /> Pioneers in ending extreme poverty</p>
                          <p className="text-muted-sm card-text-color">&nbsp; Trickle Up has been a pioneer graduating people out of extreme poverty since 1979. Weve helped more than 1.5 million of the poorest, most vulnerable people move to greater economic self-sufficiency and connection with their communities.</p>
                          
                          <p className="text-lg card-text-color card-style" ><i className="fa fa-align-justify" /> Its about more than just money</p>
                          <p className="text-muted-sm card-text-color">&nbsp; We provide the very poorest people with seed capital grants, skills training and coaching, and the support they need to create small businesses. We connect them to savings groups where they save money and access credit with others, create plans for the future and enact change in their communities. We help people connect with others in their villages, build self-confidence and find their voice because poverty is about more than just moneyits about exclusion.</p>
                         
                          <p className="text-lg card-text-color card-style"><i className="fa fa-align-justify" /> Reaching the last mile of poverty</p>
                          <p className="text-muted-sm card-text-color">&nbsp; We reach the extreme poor, those living on less than $1.90 a day, who are most economically and socially excluded. Our focus is helping women, people with disabilities, refugees and other vulnerable people who live beyond the reach of most organizations and social service programs. We go where the need is greatestto the very poorest parts of rural Asia, Africa, and the Americas.</p>
                         
                          </CardBody>
                      </Card>
                      </Col> */}

                        {/* <CardGroup> */}
                        <Card className="p-4" style={{ backgroundColor: "#2C3A5D" }}>
                          <CardBody>
                            <h1 className="text-white-50 ">Login</h1>
                            <p className="text-white-50 " style={{ color: "#ffffff" }}>
                              Sign In to your account
                            </p>
                            <FormGroup row className="text-white-50 b">
                              <Col xs="12" md="10">
                                <InputElement type="text" label="User name" name="userName" placeholder="Please enter your user name" required={this.state.userNameRequired} onChange={event => this.onChangeHandler(event)} />
                              </Col>
                            </FormGroup>
                            <FormGroup row className="text-white-50 b">
                              <Col xs="12" md="10">
                                <InputElement type="password" label="Password" name="password" placeholder="Please enter your password" required={this.state.passwordRequired} onChange={event => this.onChangeHandler(event)} />
                              </Col>
                            </FormGroup>
                            <FormGroup row>
                              <Col md="6" xs="12">
                                <Button style={{ backgroundColor: "#E84B17", color: "#ffffff", border: "none" }} onClick={this.onSubmit}>
                                  Submit
                                </Button>
                              </Col>
                              {this.state.inCorrect ? <div style={{ color: "red" }} className="help-block">
                                  Username/Password is invalid
                                </div> : null}
                            </FormGroup>
                          </CardBody>
                        </Card>
                        {/* </CardGroup> */}
                      </CardGroup>
                    </Col>
                  </Row>
                </div>
              </Container>
            </div>
          </main>
        </div>
      </div>;
  }
}
export default Login;



